import React, { PureComponent } from "react";
import DefaultNetwork from "../../../../Themes/Elements/Network/DefaultNetwork";

export default class FilterNetworks extends PureComponent {
  render() {
    const { filterIconClass, network, isDefault} = this.props;
    const isDefaultIcon = isDefault ? true : network.id === 7 || network.id === 4 ? true : false
    return (isDefaultIcon ? <DefaultNetwork network={network} networkClass={filterIconClass} /> : 
    <div className={`${filterIconClass} tb__icon tb-${network.icon}`}><div> </div></div>);
  }
}
